import React, {Component} from 'react';
import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'
import {
    Route,
    Redirect
} from 'react-router-dom';
import Header from '../component/header'
import * as actions from '../actions'
import "./style.less"
import user from '../../authority.user'
import role from '../../authority.role'
import permiss from '../../authority.permiss'
import dictionary from '../../authority.dictionary'
import demo from '../../demo'

var cookieStore = require('store')

class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            init: false
        }
    }

    componentWillMount() {
        this.props.actions.getAuthMenuListByLoginName({"userId": cookieStore.get("userInfo").id}, (list) => {
            window.USERAUTH = list
            this.setState({init: true})
        })
    }

    render() {
        const {menuResult} = this.props.home
        if (this.state.init) {
            return (
                <div id="home">
                    <Header actions={this.props.actions} menuResult={menuResult}/>
                    <Route exact path="/authority/home/demo" component={demo}/>
                    <Route exact path="/authority/home/user" component={user}/>
                    <Route exact path="/authority/home/role" component={role}/>
                    <Route exact path="/authority/home/permiss" component={permiss}/>
                    <Route exact path="/authority/home/dictionary" component={dictionary}/>
                    <Route exact path="/authority/home"
                           component={() => <Redirect to="/authority/home/user"/>}/>
                </div>
            )
        }
        return (
            <div id="home" style={{background:"#5780E0"}}>
                <div id="loading"></div>
                <div id="caseBlanche">
                    <div id="rond">
                        <div id="test"></div>
                    </div>
                    <div id="load">
                        <p>loading</p>
                    </div>
                </div>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        router: state.router,
        home: state.home
    }
}

const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        actions: bindActionCreators(actions, dispatch)
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(App)



