import React, {Component} from 'react'
import {NavLink} from 'react-router-dom'
import './style.less'
import logo from  '../../assets/logo.png'
import navR from  '../../assets/navR.png'
import navL from  '../../assets/navL.png'
import exit from '../../assets/exit.png'
import changeSystem from '../../assets/change_system.png'
var cookieStore = require('store')
export default class Header extends Component {
    logOut(){
        this.props.actions.logOutSuccess({})
    }

    render() {
        return (
            <div id="header">
                <img src={navL} alt="" className="navL"/>
                <img src={navR} alt="" className="navR"/>
                {
                    <div className="user">
                        <span className="name">您好，{cookieStore.get('userInfo')?cookieStore.get('userInfo').account:""}</span>
                        <span className="line"></span>
                        <span className="text"><a href="/authority/systemList" className="switch-system"><img className="changeSystem" src={changeSystem} alt="切换系统"/></a></span>
                        <span className="text"><a href="javascript:;" onClick={() => this.logOut()}><img className="exit" src={exit} alt="退出"/></a></span>
                    </div>
                }
               {AuthorityComponent.default(()=> {
                    return (
                        <NavLink to="/authority/home/dictionary"
                                  activeClassName='active'><span>字典管理</span></NavLink>
                     )
                 },'authority_dictionary')}
                {AuthorityComponent.default(()=> {
                    return (
                         <NavLink to="/authority/home/permiss" activeClassName='active'><span>权限管理</span></NavLink>
                    )
                },'authority_permiss')}
                {AuthorityComponent.default(()=> {
                    return (
                         <NavLink to="/authority/home/role" activeClassName='active'><span>角色管理</span></NavLink>
                    )
                },'authority_role')}
                {AuthorityComponent.default(()=> {
                    return (
                        <NavLink to="/authority/home/user" activeClassName='active'><span>用户管理</span></NavLink>
                    )
                },'authority_user')}
                <span className="logoArea"><img src={logo} alt=""/></span>
                <span className="projectName">权限管理平台</span>
            </div>
        )
    }
}



