//注销
export const LOGOUT = 'LOGOUT'
export const LOGOUT_SUCCESS = 'LOGOUT_SUCCESS'

export const logOut = (fetching) => ({
    type: LOGOUT,
    fetching
})
export const logOutSuccess = user => ({
    type: LOGOUT_SUCCESS,
    user:user
})
//查看权限
export const AUTHORITY_MENULIST_LOGINNAME = 'AUTHORITY_MENULIST_LOGINNAME'
export const AUTHORITY_MENULIST_LOGINNAME_SUCCESS = 'AUTHORITY_MENULIST_LOGINNAME_SUCCESS'
export function getAuthMenuListByLoginNameType(fetching) {
    return {
        type: AUTHORITY_MENULIST_LOGINNAME,
        fetching
    }
}
export function getAuthMenuListByLoginName(menuResult,callBack) {
    return {
        type: AUTHORITY_MENULIST_LOGINNAME_SUCCESS,
        menuResult,
        callBack
    }
}







