//注销
export function fetchAuthorityLogOut(opts,type) {
    return axios.post('/logout',opts)
        .then(function(user){
            return user.data
        }).catch(function(error){
            console.error('URL: '+ interFace+error)
        })
}
//查看权限
export function fetchAuthorityMenuListByLoginName(opts) {
    return axios.post('/authorize/user/read/permissionList', opts)
        .then(function (menuResult) {
            return menuResult.data
        }).catch(function (error) {
            console.error("URL: " + interFace + error)
        })
}




