import {call,put,take,fork} from 'redux-saga/effects'
import * as API from "../api"
import * as LoginActions from '../../login/actions'
import * as actions from '../actions'
import {push} from 'react-router-redux'
import Message from '@/components/Message'
var cookieStore = require('store')
//注销
export function* authorityLogOutPost () {
    while(true){
        yield take(actions.LOGOUT_SUCCESS);
        let posts =  yield call(API.fetchAuthorityLogOut,{});
        if(posts.httpCode === 200){
            yield put(push('/authority/login'));
            cookieStore.clearAll();
        }else{
            Message.error('注销失败！')
        }
    }
}
//查看权限
export function* authorityMenuListByLoginNamePost () {
    while(true){
        let postAction = yield take(actions.AUTHORITY_MENULIST_LOGINNAME_SUCCESS);
        yield put(actions.getAuthMenuListByLoginNameType(true))
        let posts =  yield call(API.fetchAuthorityMenuListByLoginName,postAction.menuResult);
        if(posts.httpCode === 200){
            postAction.callBack(posts.data)
        }else{
            Message.error(posts.msg)
        }
        yield put(actions.getAuthMenuListByLoginName(posts))
        yield put(actions.getAuthMenuListByLoginNameType(false))
    }
}
export default function*() {
    yield put(LoginActions.importUserInfo(cookieStore.get('userInfo')));
    yield fork(authorityLogOutPost);
    yield fork(authorityMenuListByLoginNamePost)
}



